import { SignupPageModule } from './../signup-page/signup-page.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IndexPageComponent } from './index-page.component';
import { Routes, RouterModule } from '@angular/router';
import { SigninPagesModule } from '../signin-pages/signin-pages.module';
import { SigninPagesComponent } from '../signin-pages/signin-pages.component';
import { SignupPageComponent } from '../signup-page/signup-page.component';
// import { SignupPageModule } from '../signup-page/signup-page.module';

const routes: Routes = [
  {
    path: '',
    component: IndexPageComponent,
    children: [
      {
        path: '',
        redirectTo: 'signin',
        pathMatch: 'full'
      },
      {
        path: 'signin',
        loadChildren: '../signin-pages/signin-pages.module#SigninPagesModule'
        // outlet: 'main'
      },
      {
        path: 'signup',
        loadChildren: '../signup-page/signup-page.module#SignupPageModule'
        // outlet: 'main'
      }
    ]
  }
];

@NgModule({
  declarations: [IndexPageComponent],
  exports: [IndexPageComponent],
  imports: [CommonModule, RouterModule.forChild(routes)]
})
export class IndexPageModule {}
